.betting{
    display: flex;
    align-items: flex-start;
    justify-content:flex-start;
    /* height: 100vh; */
    /* padding:20px; */
    /* background-color: #364854; */
    font-family: 'Chakra Petch', 'Montserrat', 'Passion One', 'Chakra Petch', sans-serif, cursive;
    text-transform: uppercase;
    box-sizing: content-box;
}
.betting-result{
    flex-direction: column;
    height: auto;
    /* justify-content:flex-start */
}
.trigger{
    display: flex;
    justify-content: center;
    margin: 18px 0 0;
}
.trigger li{
    font-size: 0.938rem;
    cursor: pointer;
    color: #B0B6BA;
    margin-right: 10px;
    padding:.375rem .75rem;
}
.trigger li.active{
    border-bottom:2px solid #B0B6BA;
    background-color: transparent;
}
.tab_trigger{
    display: flex;
    justify-content: center;
    margin: 8px 0 0;
}
.tab_trigger li{
    font-size: 0.938rem;
    cursor: pointer;
    color: #B0B6BA;
    margin-right: 10px;
    border:1px solid transparent;
    padding:.375rem .75rem;
}
.tab_trigger li.active{
    color: #fff;
    background-color: transparent;
}
.tab_trigger li:first-child{
    border-color: #ff6060;
    color:#ff6060;
}
.tab_trigger li:first-child:hover,
.tab_trigger li:first-child.active{
    color: #000;
    background-color: #ff6060;
    border-color: #ff6060;
}
.tab_trigger li:nth-child(2){
    border-color: #ff9f0c;
    color:#ff9f0c;
}
.tab_trigger li:nth-child(2):hover,
.tab_trigger li:nth-child(2).active{
    color: #000;
    background-color: #ff9f0c;
    border-color: #ff9f0c;
}
.tab_trigger li:nth-child(3){
    border-color: #3cd2a5;
    color:#3cd2a5;
}
.tab_trigger li:nth-child(3):hover,
.tab_trigger li:nth-child(3).active{
    color: #000;
    background-color: #3cd2a5;
    border-color: #3cd2a5;
}
.table_box{
    width: 100%;
    color: #345589;
    box-sizing: border-box;
}
.table_box h3{
    margin: 2rem 0 1rem;
    font-size: 2rem;
    font-weight: 700;
    color: #FFF;
    /* text-shadow: 0 2px 1px #454b57; */
}
.table_box .time{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 20px;
    font-size: 1.25rem;
    color: #fff;
}
.table_box .time b{
    font-size: 1.5rem;
    padding: 0 6px 0 8px;
}
.table_box .round{
    font-size: 1.25rem;
}
.table_wrap{
    padding:20px 20px 0 20px;
    background-color: rgba(46, 94, 164, .45);
    border: 1px solid #2e5ea4;
    border-radius: 6px;
    font-size: 1.5rem;
}
.list_table{
   
}
.list-header{
    width: 100%;
    display: flex;
    margin-bottom: 16px;
    border-radius: 3px;
    overflow: hidden;
}
.list-header > div,
.list-body-tr > div{
    display: flex;
    align-items: center;
    justify-content: center;
}
.list-header-th{ 
    font-weight: bold;
    font-size: 1.2rem;
}
.list-body-tr{
    position: relative;
    width: 100%;
    display: flex;
    margin-bottom: 16px;
    border-radius: 3px;
    background: linear-gradient(#fdfdfd, #a2a2a4);
    box-shadow: 0 4px 0 #043373, 1px 0 0 #5a77a4;
    overflow: hidden;
}
.list-header > div{
    display: flex;
    align-items: center;
    padding:12px 16px;
    background: linear-gradient(#d7e7fe, #b2c8f8);
}
.list-body-th{
    background: linear-gradient(#d7e7fe, #b2c8f8);
    border: 0px solid #4e73ad;
    border-width: 0px 0px 1px 2px;
    clip-path: polygon(0 0, 100% 0, 92% 100%, 0% 100%);
    z-index: 200;
}
.list-body-th:first-child{
    border-radius: 3px 0 0 3px;
}
.list-body-td{
    padding:12px 16px;
    background: linear-gradient(#fdfdfd, #a2a2a4);
}
.list-body-td:last-child{
    border-radius: 0 3px 3px 0;
}
.table_box .cols2 > *{
    width: 50%;
}
.table_box .cols3 > *{
    width: 33.333%;
}
.table_box .cols4 > *{
    width: 25%;
}

.table_box_ranking .list-header-th,
.table_box_ranking .list-body-th{
    width: 15%;
}
.table_box_ranking .list-header-td{
    width: 85%;
}
.table_box_ranking .list-body-td{
    width: 85%;
    padding-right: 18%;
}




