/* popup */

.modal{
    background-color: #1E2831;
    box-shadow: 0px 2px 10px #6d6c6c;
    color: #fff;
    position: absolute;
    top: 40px;
    left: 40px;
    right: 40px;
    bottom: 40px;
    padding: 10px 20px;
    min-width: 300px;
}
.modal:focus-visible{
    border: 0 none;
    outline:none;
}
.modal::after{
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 8px;
    background: linear-gradient(to right, #ED4096 33.3%, #fff170 33.3% 66.6%, #248aff 66.6% 99.9%) 
}
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.45);
    z-index: 900;
  }
.modal > .contents p{
    font-size: 1.125rem;
    font-weight: 700;
}
.modal > .onlyText p{
    margin-top: 0;
    font-size: 2rem;
    font-weight: 700;
    text-align: center;
}
.modal .btn-close{
    display: block;
    margin-left: auto;
    background: transparent;
    border: 0 none;
    width: 30px;
    height: 30px;
    cursor: pointer;
}
.modal .btn-close:after {
    content: "\00d7";
    position: absolute;
    right: 8px;
    top: 0;
    display: inline-block; 
    font-size:30px;
    color: #fff;
}
.modal > .actions {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    padding: 10px 0;
    margin: auto;
    text-align: center;
  }
  .modal > .actions button{
    min-width: 30%;
    padding:6px 20px;
    color: #fff;
    border:1px solid #fff;
    background-color: transparent;
    font-size: 0.875rem;
    font-weight: 400;
    border-radius: 4px;
    cursor: pointer;
  }

  .css-1okebmr-indicatorSeparator{
    display: none;
}
.ui.dropdown>.dropdown.icon:before {
    content: '\f0d7';
}
