/** Carousel **/


.carousel .carouselArrow{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}
.carousel .carouselArrow button{
    background: transparent;
    border: 1px solid #B0B5BC;
    width: 26px;
    height: 26px;
    border-radius: 3px;
}
.carousel .carouselArrow button:hover{
    background-color: rgba(255, 255, 255, 0.09);
}
.carousel .roundText{
    color:#fff;
    padding:0 20px;
}
.arrow-prev,
.arrow-next {
    position: relative;
    /* float:left; */
    border:1px solid #B0B5BC;
    margin-right:5px;
}
.arrow-prev:after {
    position: absolute;
    left: 10px; 
    top: 7px;
    content: '';
    width: 8px; /* 사이즈 */
    height: 8px; /* 사이즈 */
    border-top: 2px solid #B0B5BC; /* 선 두께 */
    border-right: 2px solid #B0B5BC; /* 선 두께 */
    transform: rotate(225deg); /* 각도 */
}

.arrow-next:after {
    position: absolute;
    right: 10px; 
    top: 7px; 
    content: '';
    width: 8px; /* 사이즈 */
    height: 8px; /* 사이즈 */
    border-top: 2px solid #B0B5BC; /* 선 두께 */
    border-right: 2px solid #B0B5BC; /* 선 두께 */
    transform: rotate(45deg); /* 각도 */
}


